
import CryptoModal from './CryptoModal.vue';
import { ref } from 'vue';

export default {
  props: {
    coins: Array,
  },
  data() {
    return {
      selectedCoin: null,
      modalActive: false,
    };
  },
  methods: {
    toggleModal() {
      this.modalActive = !this.modalActive;
    },
    updateModal(newSelectedCoin) {
      this.selectedCoin = newSelectedCoin;
    },
    selectCoin(coin) {
      this.selectedCoin = coin;
      console.log(coin);
      this.$emit('set-selected-coin', coin); // Emitting the event with the selected coin data
      this.toggleModal();
    },
  },
  components: {
    CryptoModal,
  },
};
